import { useState, useEffect } from "react";
import axios from "axios";

const useGetAssignedAddressesAPI = (week, weekday, team) => {
	const [data, setData] = useState([]);
	const [isLoaded, setIsLoaded] = useState(false);
	const [error, setError] = useState(null);

	useEffect(() => {
		const fetchAssignedAddresses = async () => {
			setIsLoaded(false);
			setError(null);

			try {
				const response = await axios.get(
					`../../../../api/get_assigned_addresses.php`,
					{
						params: {
							week: week,
						},
					}
				);

				const data = response.data;

				if (response.status === 200) {
					setData(JSON.parse(data[0].assignments));
				} else {
					setError("Fehler beim Laden der zugewiesenen Adressen");
				}
			} catch (error) {
				setError("Etwas ist schief gelaufen");
			} finally {
				setIsLoaded(true);
			}
		};

		if (week) {
			fetchAssignedAddresses();
		}
	}, [week, weekday, team]);

	return { data, isLoaded, error };
};

export default useGetAssignedAddressesAPI;
